* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  padding: 0;
  margin: 0;
  
  list-style: none;
}

.country-list-item,
.country-info-head {
    display: flex;
    align-items: center;
}

.flag-img,
.country-info-flag {
    margin-right: 15px;
}

.card-info {
  padding: 0;
  margin: 0;
  
  list-style: none;
}


.country-info-name{
   font-size: 24px;
}